body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
    outline: none;
}

.canvas > canvas {
    border: 1px solid red;
    cursor: crosshair;
}
.readonlyCanvas > canvas {
    border: none;
}

.all {
    height: 100vh;

    display: flex;
    align-items: flex-end;
    justify-content: flex-start;
}

.App {

}

.patterns {
    white-space: nowrap;
    margin-right: 44vw;
}

.patterns > div {
    margin: 0 11px;
}

.patternEditor {
    display: inline-block;
}

.patternEditor .canvases {
    display: flex;
}
.patternEditor .canvases > div {
    margin-right: 2px;
}
.patternEditor .canvases-col {
    display: flex;
    flex-direction: column;
}


.patternEditor .btns {
    height: 100px;
    padding-top: 1px;
    visibility: hidden;
    border-top: 1px solid rebeccapurple;
}
.patternEditor:hover .btns {
    visibility: visible;
}

.mainCanvas {
    display: inline-block;
}


.controls {
    position: fixed;
    top: 7px;
    left: 11px;

}

.controls  .kunteynir > div {
    margin-bottom: 7px;
}

