.color .palette {
    line-height: 0;
    width: calc(13 * (40px - 1px) + 15px);
}

.color .palette > div {
    display: inline-block;
    width: 13px;
    height: 13px;
}
.color .colorValue {
    display: inline-block;
    width: 20px;
    height: 20px;
    border: 1px solid black;
    box-sizing: border-box;
}
.color .randomnes {
    margin-top: 5px;
}

.randomes-hide {
